.newsletter{
    width: 65%;
    height: 40vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    padding: auto;
    padding: 0px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg, #c2c2c2 0%, #e1ffea22 60%);
    gap: 30px;
}
.newsletter h1{
    color: #454545;
    font-size: 55px;
    font-weight: 600;
}
.newsletter p{
    color: #454545;
    font-size: 20px;
}
.newsletter div{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: white;
    width: 730px;
    height: 70px;
    border-radius: 80px;
    border: 1px solid #e3e3e3;
}
.newsletter input{
    width: 500px;
    margin-left: 30px;
    border: none;
    outline: none;
    color: #616161;
    font-family: Poppins;
    font-size: 16px;
}
.newsletter div button{
    width: 210px;
    height: 70px;
    border-radius: 80px;
    background: black;
    color: white;
    font-size: 16px;
    cursor: pointer;
}

@media(max-width: 1280px){
    .newsletter{
        padding: 0px 80px;
        margin-bottom: 120px;
        gap: 20px;
        h1{
            font-size: 36px;
        }
        p{
            font-size: 16px;
        }
        div{
            width: 600px;
            height: 60px;
            input{
                width: 400px;
            }
            button{
                width: 160px;
                height: 60px;
            }
        }

    }
}
@media(max-width: 1024px){
    .newsletter{
        margin-bottom: 60px;
        h1{
            font-size: 30px;
        }
        p{
            font-size: 14px;
        }
        div{
            width: 500px;
            height: 50px;
            input{
                width: 300px;
                font-size: 14px;
            }
            button{
                width: 120px;
                height: 50px;
                font-size: 14px;
            }
        }

    }
}
@media(max-width: 800px){
    .newsletter{
        margin-bottom: 30px;
        height: 35vh;
        h1{
            font-size: 18px;
        }
        p{
            font-size: 12px;
        }
        div{
            width: 400px;
            height: 40px;
            input{
                width: 200px;
                font-size: 13px;
            }
            button{
                width: 100px;
                height: 40px;
                font-size: 13px;
            }
        }

    }
}
@media(max-width: 500px){
    .newsletter{
        height: 25vh;
        width: 100%;
        padding: 0;
        gap: 15px;
        h1{
            font-size: 16px;
        }
        p{
            font-size: 13px;
            max-width: 200px;
            text-align: center;
        }
        div{
            width: 290px;
            input{
                width: 130px;
            }
        }

    }
}