.relatedProducts{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 90vh;
    margin-bottom: 12vw;
    h1{
        color: #171717;
        font-size: 50px;
        font-weight: 600;
    }
    hr{
        width: 200px;
        height: 6px;
        border-radius: 10px;
        background: #252525;
    }
    .relatedproducts-item{
        margin-top: 50px;
        display: flex;
        gap: 30px;
    }
}
@media(max-width: 1280px){
    .relatedProducts{
        height: 70vh;
    }
    .relatedProducts h1{
        font-size: 40px;
    }
    .relatedProducts hr{
        width: 160px;
        height: 4px;
    }
    .relatedproducts-item{
        gap: 20px;
        margin-top: 30px;
    }

}
@media(max-width: 1024px){
    .relatedProducts{
        height: 60vh;
    }
    .relatedProducts h1{
        font-size: 30px;
    }
    .relatedProducts hr{
        width: 120px;
        height: 3px;
    }
    .relatedproducts-item{
        gap: 15px;
        margin-top: 20px;
    }

}
@media(max-width: 800px){
    .relatedProducts{
        height: 50vh;
        gap: 6px;
    }
    .relatedProducts h1{
        font-size: 20px;
    }
    .relatedProducts hr{
        width: 100px;
    }
    .relatedproducts-item{
        gap: 5px;
    }
}
@media(max-width: 500px){
    .relatedProducts{
        margin-bottom: 24vw;
        height: 90vh;
        .relatedproducts-item{
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 20px;
        }
    }
    
}
