.productdisplay{
    display: flex;
    margin: 0px 170px;
    .productdisplay-left{
        display: flex;
        gap: 17px;
        .productdisplay-img-list{
            display: flex;
            flex-direction: column;
            gap: 16px;
            img{
                height: 163px;
            }
        }
        .productdisplay-main-img{
            width: 586px;
            height: 700px;
        }
    }
    .productdisplay-right{
        margin: 0px 70px;
        display: flex;
        flex-direction: column;
        h1{
            color: #3d3d3d;
            font-size: 40px;
            font-weight: 700;
        }
        .productdisplay-right-star{
            display: flex;
            align-items: center;
            margin-top: 13px;
            gap: 5px;
            color: #1c1c1c;
            font-size: 16px;
        }
        .productdisplay-right-price{
            display: flex;
            margin: 40px 0;
            gap: 30px;
            font-size: 24px;
            font-weight: 700;
            .productdisplay-right-price-old{
                color: #818181;
                text-decoration: line-through;
            }
            .productdisplay-right-price-new{
                color: #ff4141;
            }
        }
        .productdisplay-right-size{
            h1{
                margin-top: 55px;
                color: #4e4e4e;
                font-size: 20px;
                font-weight: 600;
            }
            .productdisplay-right-sizes{
                display: flex;
                margin: 30px 0px;
                gap: 20px;
                div{
                    padding: 18px 24px;
                    background: #fbfbfb;
                    border: 1px solid #ebebeb;
                    border-radius: 3px;
                    cursor: pointer;
                }
            }
        }
        button{
            padding: 20px 40px;
            width: 200px;
            font-size: 16px;
            font-weight: 600;
            color: white;
            background: #FF4141;
            margin-bottom: 40px;
            border:  none;
            outline: none;
            cursor: pointer;
        }
        .productdisplay-right-category{
            margin-top: 10px;
            span{
                font-weight: 600;
            }
        }
    }

}
@media(max-width:1280px){
    .productdisplay{
        margin: 0px 60px;
        .productdisplay-left{
            gap: 10px;
            .productdisplay-img-list{
                gap: 10px;
                img{
                    height: 120px;
                }
              
            }
            .productdisplay-main-img{
                width: auto;
                height: 510px;
            }
        }
        
        .productdisplay-right{
            margin: 0px 30px;
            h1{
                font-size: 22px;
            }
            .productdisplay-right-star{
                gap: 3px;
                font-size: 13px;
            }
            .productdisplay-right-price{
                margin: 10px 0;
                font-size: 18px;
            }
            .productdisplay-right-description{
                font-size: 13px;
            }
            .productdisplay-right-size{
                h1{
                    margin-top: 20px;
                    font-size: 20px;
                }
                .productdisplay-right-sizes{
                    div{
                        padding: 14px 20px;
                    }
                }
            }
            button{
                width: 150px;
                padding: 15px 0px;
                margin-bottom: 20px;
            }
            .productdisplay-right-category{
                margin-top: 5px;
                font-size: 14px;
            }
        }
    }
}
@media(max-width:1024px){
    .productdisplay{
        margin: 0px 30px;
        .productdisplay-left{
            gap: 5px;
            .productdisplay-img-list{
                img{
                    height: 80px;
                }
              
            }
            .productdisplay-main-img{
                height: 350px;
            }
        }
        
        .productdisplay-right{
            h1{
                font-size: 18px;
            }
            .productdisplay-right-star{
                img{
                    width: 15px;
                }
            }
            .productdisplay-right-description{
                font-size: 12px;
            }
            .productdisplay-right-size{
                .productdisplay-right-sizes{
                    margin-top: 10px 0;
                    div{
                        padding: 10px 16px;
                        font-size: 12px;
                }
            }
            button{
                width: 120px;
                padding: 10px 0px;
                margin-bottom: 10px;
                font-size: 14px;
            }
            .productdisplay-right-category{
                font-size: 12px;
            }
        }
    }
   
}
}
@media(max-width:800px){
    .productdisplay{
        margin: 0px 10px;
        .productdisplay-left{
            gap: 0px;
            flex-direction: column-reverse;
            align-items: center;
            .productdisplay-img-list{
                flex-direction: row;
                gap: 6px;
                img{
                    height: 70px;
                }
              
            }
            .productdisplay-main-img{
                height: 300px;
            }
        }
        
        .productdisplay-right{
            h1{
                font-size: 14px;
            }
            .productdisplay-right-star{
                img{
                    width: 13px;
                }
            }
            .productdisplay-right-description{
                font-size: 10px;
            }
            .productdisplay-right-size{
                h1{
                    margin-top: 10px;
                }
                .productdisplay-right-sizes{
                    margin-top: 10px;
                    gap: 5px;
                    div{
                        padding: 4px 11px;
                }
            }
            button{
                width: 100px;
                font-size: 12px;
            }
            .productdisplay-right-category{
                font-size: 10px;
            }
        }
    }
   
}
}
@media(max-width:500px){
    .productdisplay{
        flex-direction: column;
        .productdisplay-left{
            gap: 10px;
            flex-direction: row;
            margin: auto;
            .productdisplay-img-list{
                flex-direction: column;
                gap: 8px;
                img{
                    height: 75px;
                }
            }
            .productdisplay-img{
                .productdisplay-main-img{
                    height: 330px;
                }
            }
        }
        .productdisplay-right{
            margin: 5px;
            h1{
                margin-top: 15px;
                font-size: 20px;
                font-weight: 500;
            }
            .productdisplay-right-star{
                img{
                    width: 15px;
                }
            }
            .productdisplay-right-size{
                gap: 10px;
                margin: 20px 0;
                div{
                    padding: 10px 16px;
                    font-size: 14px;
                }
            }
            button{
                width: 130px;
                font-size: 15px;
                padding: 12px 0;
            }
            .productdisplay-right-category{
                font-size: 16px;
            }
        }
    }


}